import React from 'react'

// material-ui
import { Container } from '@material-ui/core'

// custom component
import IntersectionCard from "./IntersectionCard"

/**
 * Responsive FluidContainerList container component
 * @param edges
 * @param location
 * @returns {*}
 * @constructor
 */
export default function FluidContainerList({ edges, location }) {
  return (
    <Container maxWidth={"md"} disableGutters={false}>
      {edges.map(edge => {
        const {
          node: {
            excerpt,
            timeToRead,
            fields: { slug },
            frontmatter: {
              title,
              subtitle,
              date,
              publishedBy,
            }
          }
        } = edge

        return (
            <IntersectionCard
              key={slug}
              title={title}
              subtitle={subtitle}
              excerpt={excerpt}
              publishedDate={date}
              publishedBy={publishedBy}
              timeToRead={timeToRead}
              path={slug}
              referrer={location.pathname}
            />
        )
      })}
    </Container>
  )
}
