import React, { useEffect, useRef, useState } from 'react'

// material-ui
import {
  Box,
  Button,
  Card,
  CardHeader,
  CardActions,
  CardContent,
  Typography,
  CardActionArea,
  withStyles,
} from '@material-ui/core'

// custom component
import Link from '../Links/Link'

// Build threshold array
function buildThresholdList(numSteps = 10) {
  let thresholds = []

  for (let i = 1.0; i <= numSteps; i++) {
    let ratio = i / numSteps
    thresholds.push(ratio)
  }

  thresholds.push(0)
  return thresholds
}

const styles = {
  card: {
    height: '100%',
    //    marginBottom: '8px'
  },
  actions: {
    justifyContent: 'space-between',
  },
  cardMedia: {
    height: '150px',
  },
  titleText: {
    textAlign: 'center',
  },
}

/**
 * IntersectionCard component
 * @param classes
 * @param title
 * @param subtitle
 * @param excerpt
 * @param publishedDate
 * @param timeToRead
 * @param path
 * @param referrer
 * @returns {*}
 * @constructor
 */
function IntersectionCard({ classes, title, subtitle, excerpt, publishedDate, timeToRead, path, referrer }) {
  const targetRef = useRef(null)

  // state management for intersection information
  const [entry, updateEntry] = useState({})

  /**
   * Observer callback - deals with intersection data
   * @param entries
   */
  function handleIntersections(entries) {
    entries.forEach((entry) => {
      // update local state with intersecting data
      if (entry.isIntersecting) {
        updateEntry(entry)
      }
    })
  }

  /**
   * React's built-in useEffect hook has a second argument called the "dependencies array" and it allows you
   * to optimize when React will call your effect callback. React will do a comparison between each of the
   * values (via Object.is) to determine whether your effect callback should be called.
   */
  useEffect(() => {
    /**
     * Create a new observer
     * @type {IntersectionObserver}
     */
    const observer = new IntersectionObserver(handleIntersections, {
      root: null,
      rootMargin: '0px',
      threshold: buildThresholdList(),
    })

    // Start observe the target
    if (targetRef.current) {
      observer.observe(targetRef.current)
    }

    /**
     * Clean up the observer on un-mounting
     */
    return () => observer.disconnect()
  }, [targetRef])

  return (
    <CardActionArea
      ref={targetRef}
      component={Link}
      state={{ referrer: referrer }}
      to={path}
      style={{ opacity: entry && entry.intersectionRatio, marginBottom: '8px' }}
    >
      <Card raised={entry && entry.intersectionRatio === 1} className={classes.card}>
        <CardHeader
          disableTypography={true}
          title={
            <Typography variant="h6" color="textPrimary">
              {title}
            </Typography>
          }
          subheader={
            <Typography variant="body2" color="textSecondary">
              {subtitle}
            </Typography>
          }
        />
        <CardContent>
          <Typography variant="body2">{excerpt}</Typography>
        </CardContent>
        <CardActions disableSpacing className={classes.actions}>
          <Box component="div">
            <Typography component="div" variant="caption">
              {'Date: ' + publishedDate}
            </Typography>
            <Typography component="div" variant="caption">
              {'Time to read: ' + timeToRead + ' min'}
            </Typography>
          </Box>
          <Box component="div">
            <Button size="small" color="secondary">
              Read more ...
            </Button>
          </Box>
        </CardActions>
      </Card>
    </CardActionArea>
  )
}

export default withStyles(styles)(IntersectionCard)
